export default {
	data() {
		return {
			limit: 10,
			page: 2,
			list: [],
			loading: false,
			finished: false,
			state:'',
			type:'',
			num:'',
			pid:''
		};
	},
	methods: {
		link(id){
			uni.navigateTo({
				url: `/pages/mine/setting/edit-address?id=${id}`
			})
		},
		linkTo(){
			uni.navigateTo({
				url: `/pages/mine/setting/edit-address`
			})
		},
		del(id,index){
			uni.showModal({
				title: '提示',
				content: '确定删除？',
				success: (data)=>{
					if(data.confirm){
						this.$store.dispatch('delAddress',{
							id: id
						}).then(res=>{
							this.list.splice(index,1)
						})
					}
				}
			})
		},
		getData(){
			this.$store.dispatch('getAdressList',{
				limit: this.limit,
				page: this.page
			}).then(res=>{
				this.page++
				this.list.push(...res.addrlist)
				if(res.addrlist.length<10){
					this.finished = true
				}
				this.loading = false
				uni.stopPullDownRefresh()
			}).catch(err=>{
				uni.stopPullDownRefresh()
			})
		},
		change(id){
			uni.setStorageSync('addr_id',id)
			if(this.state == 1){
				uni.navigateBack({
					delta: 1
				})
			}
		}
	},
	onShow(){
		this.page = 1
		this.list = []
		this.getData()
	},
	onLoad(e) {
		this.state = e.state
		this.type = e.type
		this.num = e.num
		this.pid = e.pid
	},
	onPullDownRefresh(){
		this.finished = false
		this.page = 1
		this.list = []
		this.getData()
	},
	onReachBottom(){
		if(!this.finished){
			this.getData()
		}
	}
}
